<template>
  <transition name="modal">
    <div class="my-popups">
      <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered" style="max-width:900px !important">
          <div class="modal-content">
            <div class="modal-header theme-primary partition-full">
              <h4 class="modal-title">{{ $t('Tag') }} {{ $t('Name') }}</h4>
              <button type="button" title="Close" class="close" v-on:click="ClosePopup">
                <em class="fa fa-times" aria-hidden="true"></em>
              </button>
            </div>
            <div class="modal-body">
              <Form ref="tagForm" autocomplete="off">
                <div class="col-md-12 float-left px-0">
                  <div class="row" style="max-height: 450px;">
                    <div class="col-md-12">
                      <div class="form-group">
                        <label><span id="">Title :</span></label><span class="text-danger">*</span>
                        <Field name="TagName" :rules="{ 'required': true }" label="Title">
                          <!-- <Multiselect v-model="tagname" :options="options" :multiple="true" :close-on-select="false" :clear-on-select="false" :preserve-search="true" placeholder="Pick some" label="name" track-by="name" :preselect-first="true">
                            <template slot="selection" slot-scope="{ values, search, isOpen }"><span class="multiselect__single" v-if="values.length" v-show="!isOpen">{{ values.length }} options selected</span></template>
</Multiselect> -->
                          <div class="input-group mb-3" style="position: relative;">
                            <input name="TagName" id="TagName" type="text" v-bind:class="{ 'form-control': true }"
                              v-model="tagname" v-on:keyup="searchTag" />
                            <div class="unique_dynamicdatalist">
                              <ul v-for="item in TagsArray" @click="setTagName(item)" style="z-index: auto;"
                                :key="item">
                                <li v-if="showTagDropdown" style="text-transform: capitalize;" :value="item.TAG_ID">
                                  {{ item.TAG_NAME }}</li>
                              </ul>
                            </div>
                            <a class="btn float-left" href="javascript:;" v-if="!FromAddProduct"
                              style="background-color: #fff;color: #303030;border-color: #abb5bf;border-left: none;"
                              @click="showTagList"><em class="dropdown-toggle float-right"></em></a>
                          </div>
                        </Field>
                      </div>
                    </div>
                  </div>
                </div>
              </Form>
            </div>
            <div class="modal-footer">
              <div class="col-md-12 p-0">
                <div class="row flex-row-reverse">
                  <div class="col-lg-6 text-right">
                    <a href="javascript:;" v-on:click="SaveTag();" title="Save" class="btn btn-success save-btn mr-1">
                      <em class="fa fa-save pr-2"></em>{{ $t('Save') }}</a>
                    <a href="javascript:;" title="Cancel" v-on:click="ClosePopup" class="btn btn-danger  delete-btn">
                      <em class="fa fa-times"></em>{{ $t('Cancel') }} </a>
                  </div>
                  <div class="col-lg-6">
                    <div class="py-2">
                      <small class="text-danger">{{ $t("MandatoryString") }} </small>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import DataService from '../../service/DataService';
import Multiselect from 'vue-multiselect'
export default {
  components: {
    Multiselect
  },
  props: {
    FromAddProduct: {
      type: Boolean,
    }
  },
  data() {
    return {
      tagname: "",
      tagId: "",
      TagsArray: [],
      showTagDropdown: false,
      options: []
    };
  },
  created: function () {
    var vm = this;
    vm.GetTagsByModuleId();
  },
  methods: {
    ClosePopup() {

      var vm = this;
      vm.$parent.CloseTagPopup();
    },


    SaveTag: function () {
      debugger;

      var vm = this;
      if (vm.tagname.length > 0) {
        var jsonobj = {
          TagId: vm.tagId == '' ? '' : vm.tagId,
          tagStatusId: 1001,
          tagName: vm.tagname.toLowerCase(),
          tagDesc: '',
          moduleName: 'Asset'
        };
        var objectPostString = JSON.stringify(jsonobj);
        DataService.SaveTag(objectPostString).then(function (response) {
          if (response.data[0].Status == "Success" && response.data[0].Value > "0") {

            vm.ShowAlert(vm.$t('Tag has been saved successfully!'), "success", true, vm.$t('Alert'));
            vm.$parent.CloseTagPopup();
            vm.$parent.GetTagsByModuleI();
          }
          if (response.data[0].Status == "Success" && response.data[0].Value == "0") {

            vm.ShowAlert(vm.$t('Tag has been updated successfully!'), "success", true, vm.$t('Alert'));
            vm.$parent.CloseTagPopup();
            vm.$parent.GetTagsByModuleI()
          }
          else if (response.data[0].Status == "Exist") {
            vm.ShowAlert(vm.$t('Tag Already Exist'), "failure", true, vm.$t('Alert'));
            vm.$parent.CloseTagPopup();
          }
        });
      }
      else {
        vm.ShowAlert(vm.$t('TagNameIsRequired'), "failure", true, vm.$t('Alert'));
      }
    },
    setTagName: function (event) {
      var vm = this;
      vm.tagname = event.TAG_NAME;
      vm.tagId = event.TAG_ID
      vm.showTagDropdown = false;
    },
    GetTagsByModuleId: function () {
      var vm = this;
      vm.TagsArray = [];
      DataService.GetTagsByModuleId('moduleName=Asset').then(function (response) {
        vm.TagsArray = JSON.parse(response.data).data;
        vm.TagsArray.forEach((itm, index) => {
          vm.options.push({
            name: itm.TAG_NAME,
            value: itm.TAG_ID
          });

        });
      });
    },

    showTagList: function () {
      var vm = this;
      vm.showTagDropdown ? vm.showTagDropdown = false : vm.showTagDropdown = true;
    },
    searchTag() {

      var vm = this;
      // vm.GetTagsByModuleId();
      vm.showTagDropdown = true;
      setTimeout(function () {
        vm.TagsArray = vm.TagsArray.filter(x => x.TAG_NAME.toLowerCase().includes(vm.tagname.toLowerCase()))
      }, 300);
    }
  },
};
</script>