    position: absolute;
    top: -24px;
    left: 144px;
}

.switch.dynamic-form .slider.round.toggle-no span {
    left: 0px !important;
    padding-right: 0 !important;
    text-align: center;
}

.switch.dynamic-form .slider.round.toggle-yes span {
    display: block !important;
    left: 0px;
    padding: 0px 2px 0px 14px !important;
}
